import React from 'react';
import "./Reels.css"

const Reels = (props) => {
  // console.log(props);
  return (
<>

<div className="reel"><span id="imageCheck"><img src={props.url.webformatURL} className="img-fluid" alt=""></img>
<button onClick={()=>window.location.href=(props.url.largeImageURL)} id="download" download>Download</button></span>
</div>

</>
  );
};

export default Reels;